<template>
  <section class="profile-section" :class="{last: lastSection}">
    <span class="dot"></span>
    <h4 class="title">{{title}}</h4>
    <div class="content">{{content}}</div>
  </section>
</template>

<script>
export default {
  props: [
    'title',
    'content',
    'lastSection',
  ],
};
</script>

<style scoped lang="less">
@import '../assets/less/colors.less';
@import '../assets/less/functions.less';

.profile-section{
  border-left: @color-bg2 2px dotted;
  padding: 0 0rem 2rem 2rem;
  position: relative;
  .dot{
    background-color: @color-bg2;
    width: 1.8rem;
    height: 1.8rem;
    .bdradius(0.9rem);
    position: absolute;
    left: -0.9rem;
    top: 0rem;
  }
  h4{
    font-weight: normal;
    font-size: 1.6rem;
    padding: 0 0 1rem;
  }
  .content{
    font-size: 1.4rem;
    line-height: 1.5;
  }
  &.last{
    border: 0 none;
    padding-bottom: 1rem;
  }
}
</style>


