<template>
  <button class="btn-gray " @click="btnClick">{{text}}</button>
</template>

<script>
export default {
  props: [
    'text',
    'click',
  ],
  methods: {
    btnClick() {
      this.click && this.click();
    }
  }
};
</script>

<style scoped lang="less">
@import '../assets/less/colors.less';
@import '../assets/less/functions.less';

.btn-gray{
  width: 100%;
  background-color: @color-btn-gray;
  border: 0 none;
  color: @color-btn-color;
  height: 4.8rem;
}
</style>


