<template>
  <div>
    <div class="logo"></div>
    <ul class="form-list">
      <form-field v-for="item in fields" 
      :key="item.id" 
      :type="item.type" 
      :name="item.name" 
      :placeholder="item.placeholder"></form-field>
    </ul>
    <div class="btn-container">
      <button-field text="注册" :click="submitRegister"></button-field>
    </div>
  </div>
</template>

<script>
import FormField from '../components/FormField';
import ButtonField from '../components/ButtonField';
export default {
  components: {
    FormField,
    ButtonField,
  },
  // name: 'Login',
  data () {
    return {
      fields: [
        {
          name: '用户名',
        },
        {
          name: '登录密码',
          type: 'password',
        },
        {
          name: '密码确认',
          type: 'password',
        },
        {
          name: '手机号码',
          type: 'tel',
        },
        {
          name: '微信号',
        },
      ],
    };
  },
  methods: {
    submitRegister() {
      this.$router.push({
        path: 'login',
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@import '../assets/less/colors.less';
@import '../assets/less/functions.less';

.logo{
  width: 16.7rem;
  height: 15.6rem;
  margin: 6rem auto;
}
.form-list{
  padding: 1rem;
  li{
    height: 4.5rem;
    border-top: @color-ededed 1px solid;
    line-height: 4.5rem;
    padding: 0 1rem;
    &:last-child{
      border-bottom: @color-ededed 1px solid;
    }
  }
}
.btn-container{
  padding: 1rem;
}
.btn{
  width: 100%;
  background-color: @color-btn-bg;
  border: 0 none;
  color: @color-ffffff;
  height: 4.8rem;
  .bdradius(0.4rem);
}
</style>


