import Vue from 'vue';
import Router from 'vue-router';
import PersonList from '@/views/PersonList';
import Profile from '@/views/Profile';
import ProfileShare from '@/views/ProfileShare';
import Register from '@/views/Register';
import Login from '@/views/Login';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'PersonList',
      component: PersonList,
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
    },
    {
      path: '/profile-share',
      name: 'ProfileShare',
      component: ProfileShare,
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
  ],
});



