<template>
  <div>
    <ul class="star-list">
      <person-card v-for="item in persons" 
        :key="item"
        :click="goRegister"
      />
    </ul>
  </div>
</template>

<script>
import PersonCard from '../components/PersonCard';
// import router from '../router';
export default {
  components: {
    PersonCard,
  },
  name: 'PersonList',
  data() {
    return {
      persons: Array(6),
    };
  },
  methods: {
    goRegister() {
      this.$router.push({
        path: 'register',
      });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
</style>



