<template>
  <button class="btn" :class="getAddClass()" @click="btnClick">{{text}}</button>
</template>

<script>
export default {
  props: [
    'text',
    'click',
    'addClass'
  ],
  methods: {
    btnClick() {
      this.click && this.click();
    },
    getAddClass() {
      return [this.addClass];
    }
  },
};
</script>

<style scoped lang="less">
@import '../assets/less/colors.less';
@import '../assets/less/functions.less';

.btn{
  width: 100%;
  background-color: @color-btn-bg;
  border: 0 none;
  color: @color-ffffff;
  height: 4.8rem;
  .bdradius(0.4rem);
}
</style>


