<template>
  <li class="list" @click="cardClick">
    <div class="photo"><img :src="(photo || require('../assets/img/photo.png'))" alt=""></div>
    <div class="intro">
      <h3 class="name">{{name || '格蕾丝'}}</h3>
      <p>{{enName || 'Grace'}}</p>
      <p>{{birth || '8月1日'}}</p>
      <p>{{constellation || '狮子座'}}</p>
      <p>{{place || '上海'}}</p>
    </div>
  </li>
</template>

<script>
export default {
  props: [
    'photo',
    'name',
    'enName',
    'birth',
    'constellation',
    'place',
    'click',
  ],
  methods: {
    cardClick() {
      this.click && this.click();
    }
  }
};
</script>

<style scoped lang="less">
.list{
  padding: 2rem;
  display: flex;
  h3{
    font-weight: normal;
  }
  .photo{
    width: 13.8rem;
    img{
      width: 100%;
    }
  }
  .intro{
    margin-left: 2rem;
    font-size: 1.6rem;
    line-height: 1.65;
    .name{
      font-size: 3.6rem;
      margin-bottom: 1rem;
    }
  }
}
</style>


