<template>
  <li class="list">
    <label>
      <span class="field">{{name}}</span>
      <input :type="type || 'text'" :placeholder="placeholder || '请输入'">
    </label>
  </li>
</template>

<script>
export default {
  props: [
    'name',
    'type',
    'placeholder',
  ],
};
</script>

<style scoped lang="less">
@import '../assets/less/colors.less';
.list{
  label{
    display: flex;
  }
  .field {
    display: block;
    width: 8rem;
  }
  input{
    color: @color-bg2;
  }
}
    
input::-webkit-input-placeholder {
  color: @color-cccccc;
}
</style>


